import { FQsearchpage } from "../fQuery";

const LOADING_GALLERY = "LOADING_GALLERY";
const SEARCH_GALLERY = "SEARCH_GALLERY";
const ADD_GALLERY = "ADD_GALLERY";
const UPDATE_GALLERY = "UPDATE_GALLERY";
const HOVER_GALLERY = "HOVER_GALLERY";
const FAIL_GALLERY = "FAIL_GALLERY";

// const queryAction = <T extends {}>(data: T) => ({
//   type: QUERY,
//   payload: data,
// });
// action type
const loadingGalleryAction = (startPage: number) => ({
  type: LOADING_GALLERY,
  payload: startPage,
});

const searchGalleryAction = (data: any) => ({
  type: SEARCH_GALLERY,
  payload: data,
});
const updateGalleryAction = (data: any) => ({
  type: UPDATE_GALLERY,
  payload: data,
});
const addGalleryAction = (data: any) => ({
  type: ADD_GALLERY,
  payload: data,
});
const hoverGalleryAction = (key: string) => ({
  type: HOVER_GALLERY,
  payload: key,
});

const failGalleryAction = () => ({
  type: FAIL_GALLERY,
  payload: "",
});

export type actionType =
  | ReturnType<typeof loadingGalleryAction>
  | ReturnType<typeof searchGalleryAction>
  | ReturnType<typeof addGalleryAction>
  | ReturnType<typeof updateGalleryAction>
  | ReturnType<typeof hoverGalleryAction>
  | ReturnType<typeof failGalleryAction>;

// search action
export const searchGalleryRequest = (params: any) => {
  return async (dispatch: any) => {
    dispatch(loadingGalleryAction(params.startPage));
    try {
      // const result = await axios.get("/api/fstore/searchpage", {
      //   params,
      // });
      const result = await FQsearchpage(params);
      dispatch(searchGalleryAction(result));
    } catch (e) {
      dispatch(failGalleryAction());
    }
  };
};

export const updateGalleryPage = (params: any) => {
  return (dispatch: any) => {
    dispatch(updateGalleryAction(params));
  };
};

export const addGalleryPage = (params: any) => {
  return (dispatch: any) => {
    dispatch(addGalleryAction(params));
  };
};

export const hoverGalleryRequest = (key: string) => {
  return (dispatch: any) => {
    dispatch(hoverGalleryAction(key));
  };
};

export interface StateProps {
  status: string;
  rowData?: any;
  comRowData?: any;
}

const initState: StateProps = {
  status: "",
  rowData: null,
  comRowData: null,
};

// reducer
const galleryReducer = (state = initState, action: actionType) => {
  switch (action.type) {
    case LOADING_GALLERY:
      return {
        status: "WAITING",
        rowData: action.payload === 0 ? null : state.rowData,
      };
    case SEARCH_GALLERY:
      return {
        status: "SUCCESS",
        rowData:
          (state.rowData && [...state.rowData, ...action.payload]) ||
          action.payload,
      };
    case ADD_GALLERY:
      return {
        ...state,
        rowData: [action.payload, ...state.rowData],
      };
    case UPDATE_GALLERY:
      return {
        ...state,
        rowData: state.rowData?.map((row: any) => {
          let newRow = {};
          if (row.key === action.payload.key) {
            // console.log("row.key");
            newRow = { ...row, ...action.payload };
          } else {
            newRow = { ...row };
          }
          return newRow;
        }),
      };
    case HOVER_GALLERY:
      const changeStateHover = state.rowData.map((row: any) => {
        if (action.payload === row.key) {
          return { ...row, hover: true };
        }
        return { ...row, hover: false };
      });
      return {
        ...state,
        rowData: [...changeStateHover],
      };
    case FAIL_GALLERY:
      return {
        status: "FAILURE",
      };
    default:
      return state;
  }
};

export default galleryReducer;
