import React from "react";
import LocationOnIcon from "@material-ui/icons/LocationOn";

function MapMarker({ title, desc, hv, $hover }: any) {
  const backColor = $hover ? "blue" : "red";
  // console.log(hv);
  return (
    <>
      {($hover || hv) && (
        <div
          style={{
            position: "absolute",
            top: "-110px",
            left: "-100px",
            fontSize: "0.9rem",
            width: "200px",
            height: "80px",
            backgroundColor: "#fff",
            opacity: "0.9",
            border: "1px solid gray",
            borderRadius: "4px",
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
          }}
        >
          <span style={{ fontWeight: 600 }}>{title}</span>
          <span>{desc}</span>
        </div>
      )}
      <div style={{ backgroundColor: "blue" }}>
        <LocationOnIcon
          style={{
            color: backColor,
            fontSize: "3.5em",
            marginLeft: "-0.5em",
            marginTop: "-1.2em",
          }}
        />
      </div>
    </>
  );
}

export default MapMarker;
