import React, { useEffect, useState } from "react";
import GoogleMapReact from "google-map-react";
import { Button } from "@material-ui/core";
import TotalPictures from "./TotalPictures";
import { connect } from "react-redux";
import { rootState } from "../../store";
import MapMarker from "./MapMarker";

const MAP_APIKEY = `${process.env.REACT_APP_MAP_API_KEY}`;

const center = {
  lat: 14.581137,
  lng: 121.163718,
};

interface GalleryMapProps {
  galleryList: any;
  handleUploadPopup: (args: any) => void;
}
interface CustomMapInfoProps {
  lat: number;
  lng: number;
  zoom?: number;
}

function GalleryMap({ galleryList, handleUploadPopup }: GalleryMapProps) {
  const [customMapInfo, setCustomMapInfo] = useState<CustomMapInfoProps>({
    lat: center.lat,
    lng: center.lng,
    zoom: 5,
  });
  const height = 715;

  useEffect(() => {
    if (galleryList) {
      const hoverGallery = galleryList.filter((gallery: any) => gallery.hover);
      if (hoverGallery.length > 0 && hoverGallery[0].geo) {
        _handleCustomMapInfo({
          lat: hoverGallery[0].geo.latitude,
          lng: hoverGallery[0].geo.longitude,
        });
      }
    }
  }, [galleryList]);

  const _handleCustomMapInfo = (obj: CustomMapInfoProps) => {
    setCustomMapInfo((prev) => ({ ...prev, ...obj }));
  };

  const _handleOnClick = (event: GoogleMapReact.ClickEventValue) => {};

  const _handleMarkerClick = (hoverKey: any, childProps: any) => {
    _handleCustomMapInfo({
      lat: childProps.lat,
      lng: childProps.lng,
      zoom: 10,
    });
  };

  const handleApiLoaded = (map: any, maps: any) => {
    // use map and maps objects
    // console.log(map);
    // console.log(maps.Marker);
  };

  const _handleMapChange = (value: GoogleMapReact.ChangeEventValue) => {
    // console.log(value);
    _handleCustomMapInfo({
      lat: value.center.lat,
      lng: value.center.lng,
      zoom: value.zoom,
    });
  };

  const _handleChildMouseHover = (hoverKey: any, childProps: any) => {
    // console.log(hoverKey);
    // console.log(childProps);
  };

  const _getPictureCount = () => {
    return (
      (galleryList &&
        galleryList.reduce((acc: any, cur: any) => {
          acc = [...acc, ...(cur.pictures || [])];
          return acc;
        }, [])) ||
      []
    );
  };
  return (
    <>
      <div style={{ position: "relative", height, width: "100%" }}>
        <div
          style={{
            width: "100%",
            height: 0,
            display: "flex",
            justifyContent: "space-between",
            alignItems: "center",
            padding: "0 80px",
            zIndex: 999,
            position: "absolute",
            top: 80,
          }}
        >
          <TotalPictures count={_getPictureCount().length} />
          <Button
            variant="contained"
            color="primary"
            style={{ opacity: 0.7 }}
            onClick={() => handleUploadPopup("")}
          >
            Upload
          </Button>
        </div>

        <GoogleMapReact
          bootstrapURLKeys={{ key: MAP_APIKEY }}
          defaultCenter={{ lat: center.lat, lng: center.lng }}
          defaultZoom={5}
          center={{ lat: customMapInfo.lat, lng: customMapInfo.lng }}
          zoom={customMapInfo.zoom}
          // resetBoundsOnResize={true}
          yesIWantToUseGoogleMapApiInternals={true}
          onGoogleApiLoaded={({ map, maps }) => handleApiLoaded(map, maps)}
          onClick={_handleOnClick}
          onChildClick={_handleMarkerClick}
          hoverDistance={30}
          onChange={_handleMapChange}
          onChildMouseEnter={_handleChildMouseHover}
        >
          {galleryList &&
            galleryList.length > 0 &&
            galleryList.map(
              (gallery: any) =>
                gallery.geo && (
                  <MapMarker
                    key={gallery.key}
                    lat={gallery.geo.latitude}
                    lng={gallery.geo.longitude}
                    title={gallery.title}
                    desc={gallery.desc}
                    hv={gallery?.hover}
                  />
                )
            )}
        </GoogleMapReact>
      </div>
    </>
  );
}

function mapStateToProps(state: rootState) {
  return {
    galleryList: state.galleryReducer.rowData,
  };
}

export default React.memo(connect(mapStateToProps)(GalleryMap));
