import React, { useState, useRef, useEffect } from "react";
import { connect } from "react-redux";
import { makeStyles, Theme } from "@material-ui/core/styles";
import {
  Modal,
  Backdrop,
  Fade,
  Button,
  CardContent,
  Typography,
  Paper,
  TextField,
  Grid,
} from "@material-ui/core";
import { DraggableDialog, CommonPopup, MapContainer2 } from "../commons";
import {
  AttachmentType2,
  deleteImageToStorage,
  FQGeoPoint,
  FQsave,
  FQtimestampNow,
  FQupdate,
  uploadImageToStorage2,
} from "../../fQuery";
import { rootState } from "../../store";
// import { useTranslation } from "react-i18next";
import SingleLineGridList from "../commons/SingleLineGridImg";
import { addGalleryPage, updateGalleryPage } from "../../store/galleryReducer";

const useStyles = makeStyles((theme: Theme) => ({
  modal: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
  paper: {
    backgroundColor: theme.palette.background.paper,
    border: "2px solid #000",
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
    width: 512,
    height: 540,
    overflow: "auto",
  },
  root: {
    backgroundColor: theme.palette.background.paper,
    width: "100%",
  },
  customButton2: {
    width: "8vw",
    minWidth: "80px",
    height: "3.5vh",
    marginLeft: theme.spacing(1),
  },
  bottom: {
    display: "flex",
    justifyContent: "flex-end",
    alignItems: "center",
  },
  gridCustom: {
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
  },
}));

interface GalleryUploadPopupProps {
  open: boolean;
  selectedRow: any;
  uid: string | null;
  handleUploadPopup: (args: any) => void;
  fnSave: (col: string, params: any) => void;
  fnUpdate: (col: string, doc: string, params: any) => void;
  updateGalleryPage: (arg: any) => void;
  addGalleryPage: (arg: any) => void;
}

let arrImages: any = [];
/**
 * Gallery Upload
 */
function GalleryUploadPopup({
  open,
  selectedRow,
  uid,
  handleUploadPopup,
  updateGalleryPage,
  addGalleryPage,
}: GalleryUploadPopupProps) {
  const classes = useStyles();
  // const { t } = useTranslation();
  const [openDialog, setOpenDialog] = useState(false);
  const [alertMsg, setAlertMsg] = useState("New");
  const fileRef = useRef<any>();
  const [attachment, setAttachment] = useState<AttachmentType2[] | null>([]);
  const [openMap, setOpenMap] = useState(false);
  const [defaultLatLng] = useState<any>({
    lat: 14.581137,
    lng: 121.163718,
  });
  const [latLng, setLatLng] = useState<any>({
    lat: null,
    lng: null,
  });
  const [inputs, setInputs] = useState({
    title: "",
    desc: "",
    fromDate: "",
    toDate: "",
  });
  const { title, desc, fromDate, toDate } = inputs;

  useEffect(() => {
    if (selectedRow) {
      selectedRow.geo &&
        setLatLng({
          lat: selectedRow.geo.latitude,
          lng: selectedRow.geo.longitude,
        });
      setInputs({
        title: selectedRow.title,
        desc: selectedRow.desc,
        fromDate: selectedRow.fromDate,
        toDate: selectedRow.toDate,
      });
      // console.log(selectedRow.pictures);
      const arrImg = selectedRow.pictures?.map((picture: any) => ({
        img: picture,
      }));
      arrImages = selectedRow.pictures;
      setAttachment(arrImg);
    }
    return () => {
      setLatLng({ lat: null, lng: null });
      setInputs({ title: "", desc: "", fromDate: "", toDate: "" });
    };
  }, [open]);
  /**
   * textfield change event
   */
  const onTextChange = (ev: React.ChangeEvent<HTMLInputElement>) => {
    const { value, name } = ev.target;
    setInputs({ ...inputs, [name]: value });
  };

  /**
   * confirm alert open
   */
  const handleClickOpenDialog = () => {
    // console.log(title, desc, fromDate, toDate);
    // if (!validationCheck()) {
    //   return false;
    // }
    if (!selectedRow) {
      setAlertMsg("New");
    } else {
      setAlertMsg("Update");
    }
    setOpenDialog(true);
  };
  /**
   * confirm alert close
   */
  const handleCloseDialog = () => {
    setOpenDialog(false);
  };

  /**
   * add file button
   * @desc input file 버튼 실행
   */
  const handleAddFile = () => {
    fileRef.current.click();
  };

  /**
   * add file input  hidden
   */
  const handleAttachFile = (ev: React.ChangeEvent<HTMLInputElement>) => {
    const target = ev.target as HTMLInputElement;
    const { files } = target;
    if (files) {
      Array.prototype.forEach.call(files, (file: File) => {
        const ext = file.type.split("/")[1];
        const reader = new FileReader();
        reader.onloadend = (finishedEvent: ProgressEvent<FileReader>) => {
          const currentTarget = finishedEvent.currentTarget as FileReader;
          const { result } = currentTarget;
          result &&
            addImg([
              {
                collection: "gallery",
                img: result.toString(),
                ext,
                isNew: true,
              },
            ]);
        };
        if (Boolean(file)) {
          reader.readAsDataURL(file);
        }
      });
    }
  };

  /**
   * add images
   */
  const addImg = (newImg: AttachmentType2[]) => {
    setAttachment((prev) => {
      if (prev) {
        return [...prev, ...newImg];
      }
      return newImg;
    });
  };

  /**
   * delete images
   */
  const deleteImg = async (args: any) => {
    if (args.isNew) {
      // 1. update attachment
      setAttachment((prev) => {
        if (prev) {
          return prev.filter((prev) => prev.img !== args.img);
        }
        return [];
      });
    } else {
      // 1. delete storage
      // console.log(args.img);
      await deleteImageToStorage(args.img);
      // 2. delete firstore
      const filterImgs = selectedRow.pictures.filter(
        (img: string) => img !== args.img
      );
      const params = { pictures: filterImgs };
      // fnUpdate("gallery", selectedRow.key, params);
      await FQupdate({ col: "gallery", doc: selectedRow.key, params });
      // 3.update gallery reducer
      updateGalleryPage({ key: selectedRow.key, ...params });
      // 4. update attachment
      // selectedRow = { ...selectedRow, ...params };
      arrImages = [...params.pictures];
      // console.log(arrImages);
      setAttachment((prev) => {
        if (prev) {
          return prev.filter((prev) => prev.img !== args.img);
        }
        return [];
      });
    }
  };

  /**
   * image upload
   */
  const uploadImages = async () => {
    const uploadImages = attachment?.filter((img) => img.isNew);

    if (uploadImages && uploadImages.length > 0) {
      const uploadedImgs = await uploadImageToStorage2(uploadImages);
      return uploadedImgs;
    }
    return [];
  };

  /**
   * insert or update
   */
  const handleUpdate = async () => {
    setOpenDialog(false);
    const arrImgUrl = await uploadImages();
    const saveImgs = arrImgUrl.map((img) => img.imgDownloadUrl); // save image download url
    await handleSave(saveImgs);
  };

  const handleSave = async (arrImgUrl?: any[]) => {
    if (!uid) {
      return;
    }
    // console.log(selectedRow);
    // const arrImgs = attachment?.map((img) => img.img);
    // console.log(arrImgs);
    // console.log(arrImages);
    // console.log(arrImgUrl);
    const arrImg = (arrImgUrl && [...(arrImages || []), ...arrImgUrl]) || [
      ...arrImages,
    ];

    const timeStampNow = FQtimestampNow();
    let params: any = {
      fromDate,
      toDate,
      title,
      desc,
      pictures: arrImg,
      geo: (latLng.lat && FQGeoPoint(latLng.lat, latLng.lng)) || null,
      uid,
      updated_at: timeStampNow,
    };
    if (!selectedRow) {
      // console.log("new");
      params = { ...params, created_at: timeStampNow };
      // const id = await fnSave("gallery", params);
      const key = await FQsave({ col: "gallery", params });
      addGalleryPage({ key, ...params });
    } else {
      // console.log("update");
      await FQupdate({ col: "gallery", doc: selectedRow.key, params });
      // update gallery reducer
      updateGalleryPage({ key: selectedRow.key, ...params });
    }
    handleUploadPopup("");
  };

  /**
   * map 팝업 open
   */
  const handleOpenMap = () => {
    setOpenMap(true);
  };

  /**
   * map 팝업 close
   */
  const handleCloseMap = (params: any) => {
    // params.key && updateGalleryPage(params);
    setOpenMap(false);
  };

  /**
   * map comfirm button event
   */
  const handleMapConfirm = (latLng: any, address: string) => {
    // console.log(latLng, address);
    setLatLng({ lat: latLng.lat, lng: latLng.lng });
    setInputs((prev) => ({ ...prev, desc: address }));
    setOpenMap(false);
  };

  return (
    <>
      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        className={classes.modal}
        open={open}
        onClose={handleUploadPopup}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={open}>
          <div className={classes.paper}>
            <Typography variant="h6" gutterBottom>
              Gallery Upload
            </Typography>
            <Paper className={classes.root}></Paper>
            <CardContent>
              <Grid item xs={12} className={classes.gridCustom}>
                <TextField
                  type="date"
                  label="From"
                  name="fromDate"
                  defaultValue={fromDate}
                  InputLabelProps={{
                    shrink: true,
                  }}
                  onChange={onTextChange}
                />
                <TextField
                  type="date"
                  label="To"
                  name="toDate"
                  defaultValue={""}
                  InputLabelProps={{
                    shrink: true,
                  }}
                  onChange={onTextChange}
                />
              </Grid>
              <Grid item xs={12} className={classes.gridCustom}>
                <TextField
                  name="title"
                  label="Title"
                  fullWidth
                  value={title}
                  onChange={onTextChange}
                />
                <Button
                  variant="contained"
                  color="primary"
                  className={classes.customButton2}
                  onClick={handleOpenMap}
                >
                  Spot
                </Button>
              </Grid>
              <TextField
                name="desc"
                label="Desc"
                fullWidth
                value={desc}
                onChange={onTextChange}
              />

              <Grid item xs={12} className={classes.gridCustom}>
                <Button
                  variant="contained"
                  color="primary"
                  onClick={handleAddFile}
                >
                  Image
                </Button>
                <input
                  type="file"
                  multiple
                  accept="image/*"
                  style={{ display: "none" }}
                  onChange={handleAttachFile}
                  ref={fileRef}
                />
              </Grid>
              {attachment && (
                <SingleLineGridList
                  imgList={attachment}
                  deleteImg={deleteImg}
                />
              )}
              <div className={classes.bottom}>
                <Button
                  variant="contained"
                  color="primary"
                  className={classes.customButton2}
                  onClick={handleClickOpenDialog}
                >
                  Save
                </Button>
              </div>
            </CardContent>
            {/* {cudStatus === "WAITING" && <LoadingIndicator />} */}
          </div>
        </Fade>
      </Modal>
      <CommonPopup
        width={"460px"}
        height={"650px"}
        open={openMap}
        handleClose={handleCloseMap}
      >
        <MapContainer2
          width={"460px"}
          height={"450px"}
          latitude={latLng.lat || null}
          longitude={latLng.lng || null}
          defaultLatLng={defaultLatLng}
          onLatLngChang={handleMapConfirm}
        />
      </CommonPopup>
      <DraggableDialog
        handleCloseDialog={handleCloseDialog}
        openDialog={openDialog}
        handleYesDialog={handleUpdate}
        alertTitle={alertMsg}
        alertMsg={`${alertMsg}?`}
      />
    </>
  );
}

function mapStateToProps(state: rootState, ownProps: any) {
  return {
    cudStatus: state.cudReducer.status,
    uid: state.loginReducer.uid,
  };
}

function mapDispatchToProps(dispatch: any) {
  return {
    updateGalleryPage: (param: any) => dispatch(updateGalleryPage(param)),
    addGalleryPage: (param: any) => dispatch(addGalleryPage(param)),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(GalleryUploadPopup);
