import React, { Fragment, useEffect, useState } from "react";
import { createStyles, Theme, makeStyles } from "@material-ui/core/styles";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import Divider from "@material-ui/core/Divider";
import ListItemText from "@material-ui/core/ListItemText";
import ListItemAvatar from "@material-ui/core/ListItemAvatar";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import Box from "@material-ui/core/Box";
import Paper from "@material-ui/core/Paper";
import { LocationOn } from "@material-ui/icons";
import { connect } from "react-redux";
import { rootState } from "../../store";
import {
  hoverGalleryRequest,
  searchGalleryRequest,
} from "../../store/galleryReducer";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      width: "100%",
      maxWidth: "36ch",
      backgroundColor: theme.palette.background.paper,
      overflowY: "scroll",
    },
    list: {
      width: "100%",
      maxWidth: "36ch",
      backgroundColor: theme.palette.background.paper,
    },
    iconFontsize: {
      fontSize: "50px",
    },
    listItem: {
      display: "flex",
      justifyContent: "space-between",
      "&:hover": {
        backgroundColor: "#dee2e6",
      },
    },
  })
);

interface TabPanelProps {
  children?: React.ReactNode;
  index: any;
  value: any;
}

function TabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && <Box p={3}>{children}</Box>}
    </div>
  );
}

interface GalleryListProps {
  galleryList: any;
  uid: string | null;
  handleUploadPopup: (args: any) => void;
  hoverGalleryRequest: (key: string) => void;
  searchGalleryRequest: (args: any) => void;
}

let lastHeight = 0;
let startPage = 0;
function GalleryList({
  galleryList,
  uid,
  handleUploadPopup,
  hoverGalleryRequest,
  searchGalleryRequest,
}: GalleryListProps) {
  const classes = useStyles();
  const [value] = useState(0);
  const rowsPerPage = 12;

  useEffect(() => {
    return () => {
      lastHeight = 0;
      startPage = 0;
    };
  }, []);
  const _handleMouseHover = (gallery: any) => {
    // console.log(gallery.key);
    hoverGalleryRequest(gallery.key);
  };

  const callUploadPopup = (gallery: any) => {
    handleUploadPopup(gallery);
  };

  const searchParams = () => {
    startPage++;
    return {
      col: "gallery",
      where: [{ uid }],
      startPage,
      maxPage: 12,
      lastRow: galleryList && galleryList[galleryList.length - 1],
    };
  };

  const searchNextClick = () => {
    searchGalleryRequest(searchParams());
  };
  /**
   * scroll event
   */
  const handleScroll = (ev: React.UIEvent<HTMLDivElement, UIEvent>) => {
    // console.log(ev.currentTarget.scrollHeight - ev.currentTarget.clientHeight);
    // console.log(ev.currentTarget.scrollTop);
    const totalHeight =
      ev.currentTarget.scrollHeight - ev.currentTarget.clientHeight;
    const currentHeight = ev.currentTarget.scrollTop;
    const isEnd = galleryList.length % rowsPerPage;
    if (
      totalHeight === currentHeight &&
      totalHeight > lastHeight &&
      isEnd === 0
    ) {
      // console.log(totalHeight, currentHeight, lastHeight);
      lastHeight = currentHeight;
      searchNextClick();
    }
  };

  return (
    <Paper className={classes.root} onScroll={handleScroll}>
      {/* <Typography color="textPrimary" align="center">
        Place
      </Typography> */}
      <Tabs
        value={value}
        // onChange={handleChange}
        indicatorColor="primary"
        textColor="primary"
        centered
      >
        <Tab label="Photos Private" />
        {/* <Tab label="Unpublished" /> */}
      </Tabs>
      <TabPanel value={value} index={0}>
        <List className={classes.list}>
          {galleryList &&
            galleryList.map((gallery: any) => (
              <Fragment key={gallery.created_at}>
                <ListItem
                  alignItems="flex-start"
                  className={classes.listItem}
                  onMouseEnter={() => _handleMouseHover(gallery)}
                  onMouseLeave={() => _handleMouseHover("")}
                  onClick={() => callUploadPopup(gallery)}
                >
                  <div>
                    <ListItemText primary={gallery.fromDate} />
                    <ListItemText
                      primary={
                        gallery.title.length > 8
                          ? `${gallery.title.substring(0, 8)}...`
                          : gallery.title
                      }
                    />
                    <ListItemText
                      primary={
                        gallery.desc.length > 8
                          ? `${gallery.desc.substring(0, 8)}...`
                          : gallery.desc
                      }
                    />
                  </div>
                  <ListItemAvatar>
                    <LocationOn className={classes.iconFontsize} />
                  </ListItemAvatar>
                </ListItem>
                <Divider variant="fullWidth" component="li" />
              </Fragment>
            ))}
        </List>
      </TabPanel>
      {/* <TabPanel value={value} index={1}>
        <div>aaa</div>
      </TabPanel> */}
    </Paper>
  );
}

function mapStateToProps(state: rootState) {
  return {
    galleryList: state.galleryReducer.rowData,
    uid: state.loginReducer.uid,
  };
}

function mapDispatchToProps(dispatch: any) {
  return {
    hoverGalleryRequest: (key: string) => dispatch(hoverGalleryRequest(key)),
    searchGalleryRequest: (args: any) => dispatch(searchGalleryRequest(args)),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(GalleryList);
