import React, { useEffect, useState } from "react";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";
import clsx from "clsx";
import { makeStyles } from "@material-ui/core/styles";
import GalleryList from "./GalleryList";
import GalleryMap from "./GalleryMap";
import { connect } from "react-redux";
import { searchGalleryRequest } from "../../store/galleryReducer";
import { rootState } from "../../store";
import GalleryUploadPopup from "./GalleryUploadPopup";

const useStyles = makeStyles((theme) => ({
  paper: {
    padding: theme.spacing(2),
    display: "flex",
    overflow: "auto",
    flexDirection: "column",
  },
  fixedHeight: {
    height: 750,
  },
}));

interface GalleryProps {
  uid: string | null;
  searchGalleryRequest: (args: any) => void;
}

function Gallery({ uid, searchGalleryRequest }: GalleryProps) {
  const classes = useStyles();
  const fixedHeightPaper = clsx(classes.paper, classes.fixedHeight);
  const [openPopup, setOpenPOpup] = useState(false);
  const [selectedRow, setSelectedRow] = useState<any>();

  const handleUploadPopup = (params: any) => {
    // params?.key && updatePage(params);
    setSelectedRow(params);
    setOpenPOpup((prev) => !prev);
  };
  const searchParams = {
    col: "gallery",
    where: [{ uid }],
    startPage: 0,
    maxPage: 12,
  };

  useEffect(() => {
    uid && searchGalleryRequest(searchParams);
  }, []);

  return (
    <>
      <Grid container spacing={3}>
        <Grid item xs={12} md={8} lg={9}>
          <Paper className={fixedHeightPaper}>
            <GalleryMap handleUploadPopup={handleUploadPopup} />
          </Paper>
        </Grid>
        <Grid item xs={12} md={4} lg={3}>
          <Paper className={fixedHeightPaper}>
            <GalleryList handleUploadPopup={handleUploadPopup} />
          </Paper>
        </Grid>
      </Grid>
      <GalleryUploadPopup
        open={openPopup}
        selectedRow={selectedRow}
        handleUploadPopup={handleUploadPopup}
      />
    </>
  );
}

function mapStateToProps(state: rootState) {
  return {
    uid: state.loginReducer.uid,
  };
}

function mapDispatchToProps(dispatch: any) {
  return {
    searchGalleryRequest: (args: any) => dispatch(searchGalleryRequest(args)),
  };
}
export default connect(mapStateToProps, mapDispatchToProps)(Gallery);
