import React from "react";
import { makeStyles } from "@material-ui/core/styles";

import CameraEnhanceOutlinedIcon from "@material-ui/icons/CameraEnhanceOutlined";

const useStyles = makeStyles((theme) => ({
  container: {
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    border: "1px solid #868e96",
    borderRadius: 5,
    width: 120,
  },
  imgCount: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
}));

interface TotalPicturesProps {
  count: number;
}
function TotalPictures({ count }: TotalPicturesProps) {
  const classes = useStyles();
  return (
    <div className={classes.container}>
      <div>
        <span>Total Pictures</span>
      </div>
      <div className={classes.imgCount}>
        <CameraEnhanceOutlinedIcon style={{ fontSize: 30, marginRight: 10 }} />
        <span>{count}</span>
      </div>
    </div>
  );
}

export default TotalPictures;
